hr {
  border: 0;
  border-top: 1px dashed #c5c5c5;
  border-bottom: 1px dashed #f7f7f7;
  margin: 20px 0;
}

.learn a {
  color: #b83f45;
  font-weight: normal;
  text-decoration: none;
}

.learn a:hover {
  color: #787e7e;
  text-decoration: underline;
}

.learn h3, .learn h4, .learn h5 {
  color: #000;
  margin: 10px 0;
  font-weight: 500;
  line-height: 1.2;
}

.learn h3 {
  font-size: 24px;
}

.learn h4 {
  font-size: 18px;
}

.learn h5 {
  margin-bottom: 0;
  font-size: 14px;
}

.learn ul {
  margin: 0 0 30px 25px;
  padding: 0;
}

.learn li {
  line-height: 20px;
}

.learn p {
  margin-top: 0;
  margin-bottom: 0;
  font-size: 15px;
  font-weight: 300;
  line-height: 1.3;
}

#issue-count {
  display: none;
}

.quote {
  border: none;
  margin: 20px 0 60px;
}

.quote p {
  font-style: italic;
}

.quote p:before {
  content: "“";
  opacity: .15;
  font-size: 50px;
  position: absolute;
  top: -20px;
  left: 3px;
}

.quote p:after {
  content: "”";
  opacity: .15;
  font-size: 50px;
  position: absolute;
  bottom: -42px;
  right: 3px;
}

.quote footer {
  position: absolute;
  bottom: -40px;
  right: 0;
}

.quote footer img {
  border-radius: 3px;
}

.quote footer a {
  vertical-align: middle;
  margin-left: 5px;
}

.speech-bubble {
  background: #0000000a;
  border-radius: 5px;
  padding: 10px;
  position: relative;
}

.speech-bubble:after {
  content: "";
  border: 13px solid #0000;
  border-top-color: #0000000a;
  position: absolute;
  top: 100%;
  right: 30px;
}

.learn-bar > .learn {
  width: 272px;
  background-color: #fff9;
  border-radius: 5px;
  padding: 10px;
  transition-property: left;
  transition-duration: .5s;
  position: absolute;
  top: 8px;
  left: -300px;
}

@media (min-width: 899px) {
  .learn-bar {
    width: auto;
    padding-left: 300px;
  }

  .learn-bar > .learn {
    left: 8px;
  }
}

